/* AddressAdd.css */

/* Style for the form container */
.form-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 20px;
  width: 100%;
  max-width: 600px;
  margin: 0 auto;

  .inputWrap {
    display: flex;
    gap: 10px;

    .col-2 {
      width: 12%;
    }

    .col-5 {
      width: 44%;
    }

    .col-6 {
      width: 50%;
    }
  }

  .submitBtnWrap {
    display: flex;
    justify-content: center;

    .submit-btn {
      background-color: #1a712e;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50px;
      width: 30%;
      color: #fff !important;
      height: 40px;
      margin: 0 auto;
      font-size: 16px;
      font-family: "SF Pro Display Medium";
      letter-spacing: 0.6px;
      @media screen and (max-width: 767px) {
        width: 40%;
        font-size: 14px;
      }
    }
  }
}

/* Style for form input fields */
.form-input {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  font-family: "SF Pro Display Regular";
  width: 100%;
  @media screen and (max-width: 767px) {
    font-size: 14px;
  }
}

/* Style for the label */
.form-label {
  font-size: 16px;
  color: #333;
  font-family: "SF Pro Display Medium";
  margin-bottom: 5px;
  display: inline-block;
  @media screen and (max-width: 767px) {
    font-size: 14px;
  }
}

/* Flex layout for two input fields in one row */
.input-group {
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

/* Make inputs take equal width */
.input-group input {
  width: 90%;
}

/* Style for submit button */
.submit-btn {
  padding: 12px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s;
  @media screen and (max-width: 767px) {
    font-size: 14px;
  }
}

.submit-btn:hover {
  background-color: #45a049;
}

.edit-address {
  padding: 0px 20px 0px 20px;
  text-align: right;
  cursor: pointer;
}

.profile-photo-main {
  display: flex;
  justify-content: center;
  justify-content: center;
}

// profile photo
.upload-container {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin-bottom: 20px;

  .upload-circle {
    width: 200px;
    height: 200px;
    cursor: pointer;
    border: 4px solid #1a712e;
    border-radius: 50%;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 50%;
    }

    .upload-icon {
      position: absolute;
      top: 90%;
      left: 65%;
      transform: translate(-50%, -50%);
      @media screen and (max-width: 767px) {
        left: 70%;
      }
      i {
        font-size: 32px;
        color: #1a712e;
      }
    }
  }
}

.newAddressBtnWrap {
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
  padding: 0 20px;
  .newAddressBtn {
    background-color: #1a712e;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
    width: 30%;
    color: #fff;
    height: 40px;
    font-size: 16px;
    font-weight: 500;
    gap: 6px;
    @media screen and (max-width: 767px) {
      font-size: 14px;
    }
    i {
      &::before {
        font-weight: 500;
      }
    }
  }
}
.editAddressBoxWrap {
  padding: 0 20px 20px;
  h3 {
    font-size: 20px;
    font-family: "SF Pro Display Medium";
    color: #1a712e;
    margin-bottom: 10px;
    @media screen and (max-width: 767px) {
      font-size: 18px;
    }
  }
  .myAddressBoxWrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 10px;

    .myaddress-getting-separate {
      border-radius: 10px;
      border: 1px solid #1a712e;
      padding: 10px;
      max-width: 49%;
      @media screen and (max-width: 767px) {
        max-width: 100%;
      }
      h4 {
        text-transform: capitalize;
        margin-bottom: 5px;
        font-size: 18px;
        font-family: "SF Pro Display Medium";
        @media screen and (max-width: 767px) {
          font-size: 16px;
        }
      }
      p {
        line-height: 1.5;
        @media screen and (max-width: 767px) {
          font-size: 14px;
        }
      }
      .defaultAddressLabel {
        font-size: 14px;
        font-weight: 600;
        color: #1a712e;
        line-height: 3;
      }
      .myAddressActionBtnWrap {
        display: flex;
        justify-content: flex-end;
        gap: 10px;
        margin-top: 10px;
        .btns {
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
          width: 35px;
          height: 35px;
          &.editBtn {
            background-color: #1a712e;
            cursor: pointer;
            i {
              font-size: 16px;
              color: #ffff;
              @media screen and (max-width: 767px) {
                font-size: 14px;
              }
            }
            &:hover {
              transition: box-shadow 0.3s;
              box-shadow: inset 0px 0px 4px 2px #092c11;
            }
          }
          &.deleteBtn {
            background-color: #e3e3e3;
            cursor: pointer;
            i {
              font-size: 16px;
              color: #9e2323;
              @media screen and (max-width: 767px) {
                font-size: 14px;
              }
            }
            &:hover {
              transition: box-shadow 0.3s;
              box-shadow: inset 0px 0px 4px 2px #616161;
            }
          }
        }
      }
    }
  }
}

.checkBoxWrap {
  display: flex;
  align-items: center;
  gap: 5px;
  margin-top: 10px;

  .form-label {
    margin-bottom: 0;
  }

  input {
    cursor: pointer;
  }
}

