}
.notificationListWrap {
  .notofiationList {
    padding: 20px;
    background-color: #3498db1a;
    .t12 {
      &.clickable {
        background-color: #1a712e;
        color: #fff;
        padding: 3px 10px;
        border-radius: 6px;
      }
    }
  }
}
.sideMenu {
  position: absolute;
  z-index: 102;
  background-color: #fff;
  width: 65%;
  height: 100%;
  left: 0;
  top: 0;
  transition: left 0.5s linear;
  overflow: hidden;
  @media screen and (max-width: 767px) {
    width: 75%;
  }
  &.hidden {
    left: -100%;
  }
  .sideMwnuHeader {
    padding: 20px 0 15px 20px;
    display: flex;
    align-items: center;
    gap: 10px;
    border-bottom: 0.2px solid #1a712e;
    img {
      @media screen and (max-width: 767px) {
        width: 40px !important;
        height: 40px !important;
      }
    }
    .sideMenuHeaderContent {
      display: flex;
      flex-direction: column;

      span {
        &:nth-child(1) {
          font-size: 18px;
          font-family: "SF Pro Display Semibold";
          color: #1a712e;
          @media screen and (max-width: 767px) {
            font-size: 16px;
          }
        }
        &:nth-child(2) {
          font-size: 16px;
          font-family: "SF Pro Display Light";
          color: #1a712e;
          @media screen and (max-width: 767px) {
            font-size: 14px;
          }
        }
      }
    }
  }
  .sideMenuList {
    padding: 20px 20px 0 20px;
    li {
      .t16 {
        &.number-of-lines-1 {
          @media screen and (max-width: 767px) {
            font-size: 14px;
          }
        }
      }
      &:last-child {
        span {
          background-color: #1a712e;
          display: flex;
          justify-content: center;
          align-items: center;
          border-radius: 50px;
          width: 100%;
          color: #fff !important;
          height: 40px;
          margin: 0 auto;
          @media screen and (max-width: 767px) {
            font-size: 14px;
          }
        }
      }
    }
  }
}
.menuItemsWrap {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  justify-content: space-between;
  row-gap: 40px;
  .innerMenu {
    width: 100%;
    height: 145px;
    border: 1px solid #1a712e;
    border-radius: 18px;
    max-width: 280px;
    @media screen and (max-width: 767px) {
      width: 100%;
      max-width: 100%;
    }
    .itemImg {
      border-radius: 18px;
      object-fit: inherit;
    }
    .home_product_category {
      font-size: 22px;
      font-family: "SF Pro Display Medium";
      @media screen and (max-width: 768px) {
        font-size: 18px;
      }
    }
  }
}
.itemWrap {
  height: 115px;

  .itemImg {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  button {
    width: 120px;
    height: 90px;
    border-radius: 10px;
    position: relative;
    overflow: hidden;
    border: 2px solid #1a712e;
    .itemImg {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  .home_product_category {
    // position: absolute;
    // bottom: 0px;
    // left: 50%;
    text-align: center;
    display: block;
    width: 100%;
    /* background-color: #1a712e; */
    color: var(--main-color);
    font-size: 16px;
    font-family: "SF Pro Display Medium";
    margin-top: 5px;
    /* font-family: sans-serif; */
    // transform: translateX(-50%);
  }
}
.proCardWrap {
  border-radius: 10px;
  position: relative;
  border: 0.1rem solid #1a712e;
  background-color: #fff;
  overflow: hidden;
  width: 100%;
  .itemImgWrap {
    height: 180px;
    width: 100%;
    display: flex;
    img {
      width: 100%;
      max-width: 100%;
      height: 100%;
    }
  }
  .boxWrap {
    padding: 10px 10px 15px;
  }
  .cartButtonWrap {
    display: flex;
    align-items: center;
    background-color: #1a712e;
    justify-content: space-evenly;
    border-radius: 50px;
    margin-top: 15px;
    width: 65%;
    margin: 15px auto 0 auto;
    .cartButton {
      padding: 8px;
      font-size: 16px;
      color: #fff;
      svg {
        path {
          stroke: #fff;
          stroke-width: 2;
        }
      }
    }
    .countNum {
      font-size: 18px;
      font-weight: 500;
      color: #fff;
    }
  }
}
.mb5 {
  margin-bottom: 20px;
}

.signInWrap {
  width: 75%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  @media screen and (max-width: 767px) {
    width: 100%;
  }
}
.ordersScreenWrapper {
  .tabs {
    margin: 20px 0;
    .tab-button {
      margin: 0;
      border-radius: 0;
      border: 1px solid #1a712e;
      color: #1a712e;
      @media screen and (max-width: 767px) {
        font-size: 14px;
      }
      &:hover {
        background-color: #1a712e;
        color: #fff;
      }
      &.active {
        background-color: #1a712e;
        color: #fff;
        &:nth-child(1) {
          border-top-left-radius: 50px;
          border-bottom-left-radius: 50px;
        }
        &:nth-child(2) {
          border-top-right-radius: 50px;
          border-bottom-right-radius: 50px;
        }
      }
      &:nth-child(1) {
        border-top-left-radius: 50px;
        border-bottom-left-radius: 50px;
      }
      &:nth-child(2) {
        border-top-right-radius: 50px;
        border-bottom-right-radius: 50px;
      }
    }
  }
  .ordersContainer {
    h2 {
      font-size: 20px;
      font-family: "SF Pro Display Medium";
      color: #1a712e;
      margin-bottom: 20px;
      @media screen and (max-width: 767px) {
        font-size: 18px;
      }
    }
    .scrollable-container {
      padding: 0;
      .card-list {
        padding: 0 20px;
        .card {
          width: 100%;
          background-color: #e9f3f6;
          padding: 10px;
          border-radius: 8px;
          border: 0.3px solid rgba(225, 224, 224, 0.9333333333);
          position: relative;
          &:hover {
            transform: none;
          }
          .topCardDataWrap {
            margin-top: 30px;
            display: flex;
            gap: 20px;
            .orderImagWrap {
              width: 120px;
              height: 120px;
              background-color: #fff;
              border-radius: 8px;
              display: flex;
              justify-content: center;
              align-items: center;
              overflow: hidden;
              @media screen and (max-width: 767px) {
                width: 70px;
                height: 70px;
              }
              img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: center;
              }
            }
            .card-info {
              margin: 0;
              flex: 1 0;
              display: flex;
              flex-direction: column;
              justify-content: flex-start;
              align-items: flex-start;
              .orderItmName {
                font-size: 18px;
                font-family: "SF Pro Display Medium";
                color: #1a712e;
                margin-bottom: 0;
                @media screen and (max-width: 767px) {
                  font-size: 14px;
                }
              }
              .orderItmWieght {
                font-size: 14px;
                margin-bottom: 5px;
                @media screen and (max-width: 767px) {
                  font-size: 12px;
                }
              }
              .orderQuantity {
                font-size: 14px;
                font-family: "SF Pro Display Regular";
                margin-bottom: 5px;
                @media screen and (max-width: 767px) {
                  font-size: 12px;
                }
              }
              .orderLD {
                font-size: 14px;
                font-family: "SF Pro Display Regular";
                color: #555;
                margin-bottom: 5px;
                @media screen and (max-width: 767px) {
                  font-size: 12px;
                }
              }
              .orderPrice {
                font-size: 18px;
                font-family: "SF Pro Display Medium";
                color: #1a712e;
                margin-bottom: 5px;
                @media screen and (max-width: 767px) {
                  font-size: 16px;
                }
              }
              .orderBalAmt {
                font-size: 14px;
                font-family: "SF Pro Display Regular";
                color: #555;
                margin-bottom: 5px;
                @media screen and (max-width: 767px) {
                  font-size: 12px;
                }
              }
            }
          }
          .dataWraps {
            display: flex;
            gap: 30px;
            margin-top: 10px;
            .wrap {
              display: flex;
              flex-direction: column;
              align-items: flex-start;
              gap: 5px;
              position: relative;
              p {
                font-size: 14px;
                font-family: "SF Pro Display Regular";
                color: #1a712e;
                margin-bottom: 0;
                @media screen and (max-width: 767px) {
                  font-size: 12px;
                }
              }
              span {
                font-size: 18px;
                font-family: "SF Pro Display Semibold";
                color: #555;
                margin-bottom: 0;
                @media screen and (max-width: 767px) {
                  font-size: 16px;
                }
              }
              &:nth-child(2) {
                &::before {
                  content: "";
                  position: absolute;
                  top: 0;
                  left: -20%;
                  height: 100%;
                  width: 1px;
                  background-color: #1a712e;
                }
              }
            }
          }
          .orderDateWrap {
            display: flex;
            margin-top: 20px;
            .box50 {
              width: 50%;
              display: flex;
              justify-content: flex-start;
              gap: 15px;
              align-items: center;
              .svgWrap {
                svg {
                  path {
                    fill: #1a712e;
                  }
                }
              }
              .innerBox {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                gap: 5px;
                p {
                  font-size: 14px;
                  font-family: "SF Pro Display Regular";
                  color: #1a712e;
                  margin-bottom: 0;
                  @media screen and (max-width: 767px) {
                    font-size: 12px;
                  }
                }
                span {
                  font-size: 18px;
                  font-family: "SF Pro Display Semibold";
                  color: #555;
                  margin-bottom: 0;
                  @media screen and (max-width: 767px) {
                    font-size: 16px;
                  }
                }
              }
              &.orderDateRightBox {
                .innerBox {
                  p {
                    color: #ff0404;
                  }
                  span {
                    color: #ff0404;
                  }
                }
              }
            }
          }
          .subExpiredText {
            text-align: center;
            display: block;
            margin-top: 20px;
            color: #ff0604;
            font-size: 16px;
            font-family: "SF Pro Display Regular";
            @media screen and (max-width: 767px) {
              font-size: 14px;
            }
          }
          .orderBtnsWraps {
            display: flex;
            gap: 10px;
            margin-top: 20px;
            // @media screen and (max-width: 767px) {
            //   flex-direction: column;
            // }
            .btn{
              width: 65%;
              height: 50px;
              display: flex;
              align-items: center;
              background-color: #1a712e;
              justify-content: space-evenly;
              border-radius: 50px;
              padding: 8px;
              font-size: 18px;
              color: #fff;
              margin: 0 auto;
              @media screen and (max-width: 767px) {
                font-size: 14px;
                width: 35%;
                height: 35px;
              }
              &:nth-child(1) {
                background-color: #ff0404;
              }
              &:nth-child(2) {
                background-color: #fff;
                color: #1a712e;
                border: 1px solid #1a712e;
              }
            }
          }
          .subscription_idWrap {
            position: absolute;
            left: 10px;
            top: 10px;
            height: 30px;
            width: auto;
            font-size: 16px;
            font-family: "SF Pro Display Semibold";
            color: #555;
            @media screen and (max-width: 767px) {
              font-size: 14px;
            }
          }
          .statusWrap {
            position: absolute;
            right: 0;
            top: 0;
            height: 30px;
            width: auto;
            padding: 0 10px;
            background-color: #ff0404;
            display: flex;
            justify-content: center;
            align-items: center;
            border-bottom-left-radius: 8px;
            span {
              color: #fff;
              font-size: 14px;
            }
          }
        }
      }
    }
  }
}
.walletSection {
  padding: 20px;
  .walletContainer {
    h3 {
      font-size: 20px;
      font-family: "SF Pro Display Medium";
      color: #1a712e;
      margin-bottom: 20px;
      text-align: center;
      @media screen and (max-width: 767px) {
        font-size: 18px;
      }
    }
    .topSection {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 20px;
      .waltLeftBox {
        span {
          font-size: 16px;
          font-family: "SF Pro Display Regular";
          color: #555;
          @media screen and (max-width: 767px) {
            font-size: 14px;
          }
        }
        p {
          font-size: 20px;
          font-family: "SF Pro Display Medium";
          color: #555;
          text-transform: capitalize;
          @media screen and (max-width: 767px) {
            font-size: 18px;
          }
        }
      }
      .waltRightBox {
        width: 50px;
        height: 50px;
        background-color: #1a712e;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        span {
          svg {
            path {
              fill: #fff;
            }
          }
        }
      }
    }
    .walletBalanceText {
      font-size: 18px;
      font-family: "SF Pro Display Regular";
      color: #1a712e;
      margin-bottom: 20px;
      span {
        font-size: 20px;
        font-family: "SF Pro Display Semibold";
        @media screen and (max-width: 767px) {
          font-size: 18px;
        }
      }
      @media screen and (max-width: 767px) {
        font-size: 16px;
      }
    }
    .pointsWrap {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .pointsBox {
        width: 48%;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        background: #1a712e;
        padding: 15px 0;
        border-top-left-radius: 16px;
        border-top-right-radius: 16px;
        .spanSvgBox {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 40px;
          height: 40px;
          background: #f3f3f3;
          border-radius: 50%;
        }
        .rightPart {
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 5px;
          span{
            &:nth-child(1){
              font-size: 16px;
              font-family: "SF Pro Display Semibold";
              color: #fff;
              @media screen and (max-width: 767px) {
                font-size: 14px;
              }
            }
            &:nth-child(2){
              font-size: 26px;
              font-family: "SF Pro Display Regular";
              color: #fff;
              @media screen and (max-width: 767px) {
                font-size: 24px;
              }
            }
          }
        }
      }
    }
  }
}
